* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
.mainContainer {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to right, #009496 0%, #9C27B0 100%);
  .formContainer {
    width: 420px;
    height: 480px;
    position: relative;
    background-color: #fff;
    border: 1px solid gray;
    border-radius: 10px;
    padding: 5px;
    overflow: hidden;
    .buttonBox {
      width: 240px;
      margin: 35px auto;
      position: relative;
      box-shadow: 0 0 20px 9px #ff61241f;
      border-radius: 30px;
      #btn {
        position: absolute;
        background-image: linear-gradient(to right, #00BCD4 0%, #f9d423 100%);
        top: 0;
        left: 0;
        width: 110px;
        height: 100%;
        border-radius: 30px;
        transition: 0.5s;
      }
      .toggle_btn {
        padding: 10px 30px;
        cursor: pointer;
        background-color: transparent;
        border: none;
        outline: none;
        position: relative;
      }
    }
  }
}
#SignIn,
#SignUp {
  top: 120px;
  width: 300px;
  position: absolute;
  transition: 0.5s;
  .input {
    width: 100%;
    padding: 10px 0;
    margin: 5px 0;
    border: none;
    border-bottom: 1px solid #999;
    outline: none;
    background-color: transparent;
  }

  .buttonss {
    background-image: linear-gradient(to right, #00BCD4 0%, #f9d423 100%);
    outline: none;
    border: 0;
    outline: none;
    width: 85%;
    padding: 10px 30px;
    cursor: pointer;
    display: block;
    margin: auto;
    border-radius: 30px;
    color: whitesmoke;
    &:hover {
      background-image: none !important;
      background-color: rgba(44, 130, 201, 0.9);
      color: black;
    }
    &:focus {
      outline: none;
    }
  }
}
.handle {
  margin: 30px 0;
  .inputcheck {
    cursor: pointer;
  }
  .span {
    color: #777;
    font-size: 12px;
    margin: 0 10px;
  }
}
#SignIn {
  left: 50px;
}
#SignUp {
  left: 450px;
}
.googlebtn {
  background-image: linear-gradient(to right, #00BCD4 0%, #f9d423 100%);
  border: 0;
  outline: none;
  width: 85%;
  padding: 10px 30px;
  cursor: pointer;
  display: block;
  margin: auto;
  border-radius: 30px;
  color: whitesmoke;
  transition: all 0.5s ease;
  &:hover {
    background-image: none !important;
    background-color: rgba(44, 130, 201, 0.9);
    color: black;
  }
  &:focus {
    outline: none;
  }
}
@media only screen and (max-width: 786px) {
  .formContainer {
    border: none !important;
  }
  .buttonss,
  .googlebtn {
    margin: 0 !important;
  }
  .or {
    display: "flex";
    justify-content: "center";
    margin-right: 165px !important;
  }
}
